/* Apply a reset to remove default styles */
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

/* Set a background color for the page */
.main {
    background-color: #f0f0f0;
    height: 100vh;
}

/* Style the header/navbar */
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #333;
    color: #fff;
    height: 15%;
    width: 100%;
    padding: 0 200px;
    font-size: 1.2rem;
}

/* Style the links in the header/navbar */
.header a {
    color: #fff;
    text-decoration: none;
    margin: 0 10px;
    font-weight: bold;
}

/* Style the main content area */
.body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 70%;
    padding: 20px;
    font-size: 1.2rem;
}

/* Style the links in the main content area */
.body a {
    color: #666;
    text-decoration: none;
    margin: 0 10px;
}

.body .rounded-image{
    border-radius: 50%;
    width: 200px;
    height: 200px;
    object-fit: cover;
    
}

/* Style the headings */
h1, h3 {
    margin: 10px 0;
    text-align: center;
}

/* Set a color scheme */
a:hover {
    color: #6a5acd;
}

.footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.footer-links {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

.logo-image {
    width: 30px;
    height: 30px;
}
  
  